@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Nunito:ital,wght@0,400;0,600;0,700;0,800;0,900;1,300&family=Open+Sans:wght@400;500;600;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	/* background-color: #f3f3f3; */
	/* background-color: #f6f6f6; */
	background-color: #fbf1f2;
	background-color: var(--bg-accent);
	font-weight: 500;
	font-size: 14px;
	line-height: 1.5em;
	color: #333;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
	--calc-heading: #cb4154;
	--bg: #242526;
	--bg-accent: #fbf1f2;
	/* --bg-accent: #daecf9; */
	--inactive: #b5b5b5;
	--primary: #fbf1f2;
	/* --primary: #5ea0f6; */
	--bluePrimary: #1877f2;
	--blueAccent: #abc7e6;
	--text-color: #dadce1;
	--nav-size: 60px;
	--border: 1px solid #474a4d;
	--border-radius: 8px;
	--speed: 500ms;
	--hover: 180ms;
	--boxShadow: 1px 1px 4px 1px #00000026;
	--openSans: 'Open Sans', sans-serif;
	--nunito: 'Nunito', sans-serif;
	--lato: 'Lato', sans-serif;
}

body,
p {
	font-family: 'Open Sans', sans-serif;
	font-family: var(--openSans);
}

h1 {
	font-family: 'Open Sans', sans-serif;
	font-family: var(--openSans);
	font-weight: 800;
	margin-bottom: 15px;
}

h2 {
	font-family: 'Nunito', sans-serif;
	font-family: var(--nunito);
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
	color: #dadce1;
	color: var(--text-color);
	text-decoration: none;
}

.title {
	text-align: center;
}

ol {
	margin-left: 30px;
}

li {
	margin: 12px 0;
}

ul,
menu,
dir {
	display: block;
	list-style-type: disc;
	-webkit-margin-before: 1em;
	-webkit-margin-after: 1em;
	-webkit-margin-start: 0px;
	-webkit-margin-end: 0px;
	-webkit-padding-start: 40px;
}

.radio-input-wrapper {
  position: relative;
  background: white;
  display: inline-block;
  border-radius: 2px;
  border: 1px solid #dadada;
}

.radio-input-wrapper input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  width: 0;
  height: 0;
}

.radio-input-wrapper .label {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.radio-input-wrapper .label .type {
  font-size: 14px;
  font-weight: 600;
}

.radio-input-wrapper .label .mg-amount {
  font-size: 11px;
  font-weight: 500;
}

.radio-input-wrapper .label input:checked ~ .checkbox {
  border-radius: 2px;
  color: var(--calc-heading);
  box-shadow: 0px 0px 0px 2px var(--calc-heading);
  font-weight: bold;
}

.radio-input-wrapper .label .checkbox {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 22px 0;
  width: 100%;
  text-align: center;
  height: 100%;
  cursor: pointer;
  -webkit-transition: 140ms;
  transition: 140ms;
  min-width: 100px;
}

.radio-input-active {
  border: 1px solid var(--blueAccent);
}
