.metric-type-container {
  margin-left: 16px;
  display: flex;
  align-items: center;
}

.metric-type-container .metric-type {
  position: relative;
  font-size: 14px;
  color: var(--inactive);
  -webkit-transition: 250ms;
  transition: 250ms;
  cursor: pointer;
}

.metric-type-container .metric-active {
  color: var(--calc-heading);
  font-family: var(--nunito);
  font-weight: bold;
  opacity: 1;
}

.metric-type-container .metric-type:nth-child(2) {
  padding-left: 15px;
  margin-left: 15px;
}

.metric-type-container .metric-type:nth-child(2):after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 2px;
  height: 18px;
  background-color: #e6e6e6;
}
