.group-wrapper {
  position: relative;
  background-color: #ffffff;
  margin: 10px;
  height: 98%;
  width: 100%;
  padding: 15px 25px;
  font-size: 14px;
  box-shadow: 1px 1px 2px 1px #0000000d;
